:root {
  --light-beige: #f8f8f4;
  --charcoal: #171717;
  --green-link: var(--bright-green);
  --black: #080808;
  --white: white;
  --dark-green:  #232f1f;
  --transparent: transparent;
  --dark-beige2: #e5e3db;
  --light-green:  #232f1f;
  --dark-beige: #ebe9e1;
  --black1: #171717;
  --bright-green: #3c4637;
  --lgt-font: Feature Display, Times New Roman, sans-serif;
}

.downcomponent {
  box-sizing: border-box;
  font-family: Circular, "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: lighter;
  color: rgb(240, 233, 225);
  will-change: background;
  transition: background-color 0.8s;

}

.downcomponent.in-view {
  animation: colorChange 2s forwards; /* Adjusted duration here */
  animation-play-state: paused;
}

.downcomponent.in-view.animation-play-state {
  animation-play-state: running;
}

@keyframes colorChange {
  from {
    background-color: white; /* Initial background color */
  }
  to {
    background-color: var(--light-green); /* Target Color */
  }
}
.extra_contents {
  width: 100%;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 40px;
  height: 200px;
  position: relative;
}

.text p {
  color: white;
  font-size: 10px;
  letter-spacing: 2px;
  font-weight: lighter;
  font-family: 'Courier New', Courier, monospace;
}

.menue_options {
  z-index: 10;
  grid-column-gap: 1.5rem;
  grid-row-gap: 1.5rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.menue_bottom {
  display: flex;
  height: 120px;
  margin-bottom: 20px;
  margin-top: 20px;
  align-items: center;
  box-sizing: border-box;
  padding: 10px;
  font-weight: bold;
  flex-direction: row;
}

.logo {
  height: 100px;
  width: 200px;
}

.modals {
  position: fixed;
  top: 85%;
  margin-right: 20px;
  flex-direction: column;
  display: flex;
  justify-content: space-around;
  z-index: 99999999;
  left: 85%;
}

.our_socials {
  height: unset;
  padding: 5px;
  width: 100%;
  color: white;
  text-decoration: none;
  display: flex;
  font-size: 30px;
  flex-direction: column;
}

.our_socials ul {
  display: flex;
  flex-direction: row;
  height: unset;
  margin: 10px;
  width: 300px;
  padding: 5px;
  list-style: none;
}

.our_socials button {
  background-color: transparent;
  font-size: 18px;
  color: white;
  width: 200px;
  border: none;
}

.goto_top {
  height: 40px;
  margin: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  border-radius: 50%;
  backdrop-filter: blur(15px);
}

.message {
  height: 40px;
  margin: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  border-radius: 50%;
  background-color:#000020;
}

.extra_contents button {
  width: 80%;
  font-size: 14px;
  border-radius: 10px;
  height: 180px;
  position: absolute;
  top: 60px;
  color: black;
  background-color: whitesmoke
}

.container_downbar {
  position: relative;
  width: 100vw;
  padding-top: 10px;
  padding-bottom: 10px;
  max-height: 50vh;
  height: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
}
.background-image {
  width: 60%;
  min-height: 200px;
  height: 80%;
  min-height: 200px;
  display: block;
  opacity: 0.4;
  max-width: 350px;
  
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 20px;
  background-color: transparent;
  box-sizing: border-box;
}

.menu-options {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu-options li {
  margin: 10px 0;
  font-size: 1.5em;
  cursor: pointer;
}

.our_socials ul li {
  height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
  padding: 0;
  box-sizing: border-box;
  margin-top: 0px;
  background-color: transparent;
  border-radius: 5px;
  border-radius: 5px;
}

