.sunnyside-unique-footer {
  background-color:transparent;
  padding: 40px 20px;
  text-align: center;
  width: 100%;
  box-sizing: border-box;
}

.sunnyside-unique-footer-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sunnyside-unique-footer-title {
  color: #ffffff;
  font-size: clamp(20px, 5vw, 24px);
  font-weight: bold;
  margin-bottom: 20px;
}

.sunnyside-unique-social-icons {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
}

.sunnyside-unique-icon {
  color: #ffffff;
  font-size: clamp(20px, 4vw, 24px);
  cursor: pointer;
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.sunnyside-unique-icon:hover {
  opacity: 0.7;
  transform: scale(1.1);
}

@media screen and (max-width: 768px) {
  .sunnyside-unique-footer {
    padding: 30px 15px;
  }

  .sunnyside-unique-social-icons {
    gap: 15px;
  }
}

@media screen and (max-width: 480px) {
  .sunnyside-unique-footer {
    padding: 20px 10px;
  }

  .sunnyside-unique-social-icons {
    gap: 10px;
  }
}
